.navbar{
    display: flex;
    justify-content: space-between;
    background-color: #222;
    padding: 1em;
}

.list{
    display: flex;
    list-style: none;
    align-items: center;
}
.item{
    margin-right: 1em;
}
.item a{
    color: #fff;
    text-decoration: none;
    transition: 0.5s;

}
.item a:hover{
    color: #ffBB33;
}
.logo:hover {
    width: 61px;
    height: 61px;
}


